main {
  position: relative; }

body {
  background: #1f1f1f; }

.container {
  max-width: 1440px;
  margin: 0 auto; }

.unselectable {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Chrome/Safari/Opera */
  -khtml-user-select: none;
  /* Konqueror */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                    not supported by any browser */ }

.section__wrapper {
  min-height: 100vh; }

.padding_sect {
  padding-top: 100px; }

.upper_text {
  text-transform: uppercase; }

.absolute_center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.main__title {
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  font-weight: 700; }
  .main__title_2 {
    font-size: 45px; }
  .main__title_3 {
    font-size: 38px; }
  .main__title_4 {
    font-size: 36px; }
  .main__title_5 {
    font-size: 24px; }
  .main__title_6 {
    font-size: 21px; }
  .main__title_7 {
    font-size: 18px; }
  .main__title_8 {
    font-size: 16px; }

.standart_title {
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  font-weight: 500; }
  .standart_title_5 {
    font-size: 18px; }

.sub_title {
  font-weight: normal;
  font-stretch: normal;
  font-style: normal; }
  .sub_title_3 {
    font-size: 20px; }
  .sub_title_4 {
    font-size: 16px; }
  .sub_title_5 {
    font-size: 14px; }
  .sub_title_6 {
    font-size: 12px; }

.color_yellow {
  color: #ffd10a; }

.color_orange {
  color: #f28500; }

.color_black {
  color: #272727; }

.color_black_2 {
  color: #8d8d8d; }

.color_white {
  color: #fefefe; }

.color_green {
  color: #b1d628; }

@media (max-width: 1450px) {
  .container {
    width: 90%;
    margin: 0 auto; } }

@media (max-width: 992px) {
  .main__title_3 {
    font-size: 24px; } }

@media (max-width: 690px) {
  .main__title_2 {
    font-size: 25px; }
  .standart_title_5 {
    font-size: 14px; } }

@media (max-width: 710px) {
  .padding_sect {
    padding-top: 70px; } }

@media (max-width: 588px) {
  .main__title_5 {
    font-size: 18px; } }

@media (max-width: 400px) {
  .main__title_7 {
    font-size: 15px; }
  .main__title_8 {
    font-size: 14px; } }
